import { Box } from "@material-ui/core";
import React from "react";
import useStyles from "./AboutMeStyles";

const BackgroundDetails = () => {
    const classes = useStyles();
    return (
        <Box>
            <p className={classes.details}>
                My name is Ally, I am 10 years old. I have been selling freeze dried candy for almost two years and Im now officially launching Allys Sweets.
                I run a small business with the help from my mom, grandma, and brother. I am saving for school tuition, a trip India in 2024, and putting money
                in my savings for my future. I hope that Allys Sweets makes my customers happy and brings a smile to their faces.
                <br/>
                <br/>
                <span className={classes.thankYouSpan}>
                    Thank you for your support!
                </span> 
            </p>
        </Box>
    );
}

export default BackgroundDetails;   